export default {
	data(){
		return{
			limt:'',
			passengerList:[],
			submitType:'',
		}
	},
	onLoad(options){
		//传参
		//submitType参数为1,为旅游进入
		//submitType参数为2,为出租车进入
		//submitType参数为3,为传统客运进入
		//limitNum参数为你限制添加乘车人的数量（大于等于1）
		this.submitType=options.submitType;
		this.limit=options.limitNum;
		uni.getStorage({
			key:'userInfo',
			fail() {
				uni.showToast({
					icon:'none',
					title:'暂未登录,请登录后查看'
				})
				uni.stopPullDownRefresh();
				setTimeout(function(){
					uni.navigateTo({	
						//loginType=1,泉运登录界面
						//loginType=2,今点通登录界面
						//loginType=3,武夷股份登录界面
						url  : '/pages/GRZX/userLogin?loginType=1'
					}) 
				},500);
			}
		})
	},
	onPullDownRefresh:function(){
	  this.loadData();
	},
	onShow() {
		//uni.startPullDownRefresh();
		this.loadData();
	},
	methods:{
		async loadData(){
			var that=this;
			var array=[];
			var list=[];
			uni.getStorage({
				key:"passengerList",
				success(res2) {
					for(var j=0;j<res2.data.length;j++){
						list.push(res2.data[j].passengerId);
					}
				}
			})
			uni.getStorage({
				key:'userInfo',
				success(res){
					if(res.data.userId==""||res.data.userId==null){
						uni.showToast({
							icon:'none',
							title:'暂未登录,请先登录'
						})
					}else{
						uni.request({
							//url:'http://111.231.109.113:8002/api/person/userInfoList',
							url:that.$GrzxInter.Interface.userInfoList.value,
							data:{
								userId:res.data.userId
							},
							method:that.$GrzxInter.Interface.userInfoList.method,
							success(res1) {
								console.log(res1,'111')
								for(var i=0;i<res1.data.data.length;i++){
									if(res1.data.data[i].userSex==0){
										res1.data.data[i].userSex="男";
									}else{
										res1.data.data[i].userSex="女";
									}
									var data1=res1.data.data[i];
									//console.log(data1,"data1")
									data1.hiddenIndex=0;
									for(var q=0;q<list.length;q++){
										if(data1.passengerId==list[q]){
											data1.hiddenIndex=1;
										}
									}
									array.push(data1);
									uni.stopPullDownRefresh();
								}
							}
						})
					}
					
				}
			})
			this.passengerList=array;
			console.log(array)
		},
		addPassenger(){
			uni.getStorage({
				key:'userInfo',
				success() {
					uni.navigateTo({
						url:'/pages/GRZX/addPassenger?type=ad'
					})
				},
				fail() {
					uni.showToast({
						icon:'none',
						title:'暂未登录,无法添加乘客'
					})
				}
			})
			
		},
		returnPages(){
			uni.navigateBack();
		},
		editPassenger(e){   //编辑乘车人信息
			uni.setStorage({
				key:'editPassenger',
				data:e
			})
			uni.navigateTo({
				url:'/pages/GRZX/addPassenger?type=edit'
			})
		},
		choosePassenger(e){  //选择乘车人
			//console.log(e,"....00000")
			var list=this.passengerList;
			var count=0;
			for(var i=0;i<list.length;i++){
				if(list[i].hiddenIndex==1){
					count++;
				}
			}
			if(e.userauditState=='审核通过'||e.userType=="儿童"||e.userType=="成人"||e.userType=="老人"){
				if(e.hiddenIndex==1){
					e.hiddenIndex=0;
				}else if(count>(this.limit-1) && this.submitType==2){
					uni.showToast({
						title: '乘客最多只能添加'+this.limit+'名',
						icon:"none"
					});
				}else{
					e.hiddenIndex=1;
				}		
			}else{
				uni.showToast({
					title: '该审核未通过或待审核，无法选用',
					icon:"none"
				});
			}
		},
		definite(){ //提交array
			var data=this.passengerList;
			var array=[];
			for(var i=0;i<data.length;i++){
				if(data[i].hiddenIndex==1){
					array.push(data[i]);
				}
			}
			if(array.length==0){
				uni.showToast({
					title: '请选择乘客',
					icon:"none"
				})
			}else{
				uni.setStorage({
					key:"passengerList",
					data:array
				})
				uni.navigateBack();	
			}			
		}
	}
}
